<template>
    <v-container style="height: 100%; position: relative; width: 100%" align="center">
        <PublicProfile>

        </PublicProfile>

        <v-row style="height: 100%">
            <v-col cols="12"
                   sm="6" offset-sm="3"
                   md="4" offset-md="4"
                   style="height: 100%">

                <v-container class="bottom-centered-buttons">
                    <v-btn block color="primary" @click="logout">Cerrar sesión</v-btn>
                </v-container>
            </v-col>
        </v-row>
    </v-container>

</template>

<script>
    import PublicProfile from "./PublicProfile";
    import * as firebase from "firebase";

    export default {
        name: "ProfileHome",
        data: ()=> ({
        }),
        methods:{
            logout(){
                firebase.auth().signOut()
                    .then(()=>{
                        this.$router.push({name:'login'})
                    })
                    .catch(err=>{
                        console.log(err)
                    })
            }
        },
        components: {PublicProfile}
    }
</script>

